///
@keyframes fade-in-move {
  to {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes scale {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

[class*="block--animation"] {
  animation-play-state: paused !important;

  * {
    animation-play-state: paused !important;
  }

  &.do--animation {
    animation-play-state: running !important;

    * {
      animation-play-state: running !important;
    }
  }
}

.do--fade-in {
  opacity: 0;
  animation: fade-in-move 1s 0s ease forwards;
}

.do--fade-in-up {
  opacity: 0;
  transform: translateY(-50px);
  animation: fade-in-move 1s 0s ease forwards;
}

.do--fade-in-down {
  opacity: 0;
  transform: translateY(50px);
  animation: fade-in-move 1s 0s ease forwards;
}

.do--scale-up {
  opacity: 0;
  transform: scale(.9);
  animation: scale 1s 0s ease forwards;
}

.do--scale-down {
  opacity: 0;
  transform: scale(1.1);
  animation: scale 1s 0s ease forwards;
}

@for $i from 0 through 5000 {
  @if $i % 100 == 0 {
    .do--animation-delay-#{$i} { 
      animation-delay: #{$i}ms
    }

    .do--animation-duration-#{$i} { 
      animation-duration: #{$i}ms
    }
  }
}